.halloweenPumpkinContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*transform: scale(0,6);*/
}

.halloweenPumpkin {
    height: 99px;
    width: 128px;
    background: #f68632;
    border: 4px solid #a14907;
    border-radius: 70%/110%;
    position: relative;
    box-shadow: inset 0 0 20px #a14907, 0 0 30px -4px red;
    z-index: 1;
    -webkit-animation: float 3s infinite;
            animation: float 3s infinite;
}

.halloweenPumpkinTexture {
    height: 99px;
    width: 128px;
    position: absolute;
    bottom: -3px;
    border-radius: 70%/130%;
    border-bottom: 4px solid #a14907;
    border-top: 4px solid #a14907;
}

.halloweenPumpkinTexture::after {
    content: '';
    display: block;
    height: 99px;
    width: 102px;
    position: absolute;
    bottom: -3px;
    left: 12px;
    border-radius: 70%/130%;
    border-bottom: 4px solid #a14907;
    border-top: 4px solid #a14907;
}

.halloweenPumpkinRoot {
    background: #517f54;
    height: 24px;
    width: 10px;
    position: absolute;
    left: 59px;
    top: -23px;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
    border-radius: 30% 20% 70% 10%;
    z-index: 1;
    box-shadow: inset 0 0 10px #29412b;
}

.halloweenPumpkinEye {
    position: absolute;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    top: 24px;
}

.halloweenPumpkinEye.Right {
    right: 31px;
    border-left: 20px solid #401b03;
    border-bottom: 18px solid transparent;
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}

.halloweenPumpkinEye.Left {
    left: 31px;
    border-right: 20px solid #401b03;
    border-bottom: 18px solid transparent;
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}

.halloweenPumpkinMouth {
    border-bottom: 8px solid #401b03;
    position: absolute;
    width: 34px;
    height: 17px;
    bottom: 12px;
}

.halloweenPumpkinMouth.Right {
    right: 30px;
    border-radius: 0 0 100% 0;
}

.halloweenPumpkinMouth.Left {
    left: 30px;
    border-radius: 0 0 0 100%;
}

.halloweenPumpkinTeeth {
    border-left: 7px solid #401b03;
    border-bottom: 0 solid transparent;
    border-top: 7px solid transparent;
    width: 4px;
    height: 8px;
    position: absolute;
    top: 72px;
    left: 61px;
}

.halloweenPumpkinShadow {
    margin: 10% auto 0;
    background: #222222;
    width: 115px;
    height: 25px;
    border-radius: 50%;
    -webkit-animation: zoom 3s infinite;
            animation: zoom 3s infinite;
}

@-webkit-keyframes float {
    0%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    50% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }
}

@keyframes float {
    0%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    50% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }
}

@-webkit-keyframes zoom {
    0%, 100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
}

@keyframes zoom {
    0%, 100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
}

@-webkit-keyframes light {
    0%, 100% {
        border-right-color: #401b03;
        border-left-color: #401b03;
    }
    50% {
        border-right-color: #E9C608;
        border-left-color: #E9C608;
    }
}

@keyframes light {
    0%, 100% {
        border-right-color: #401b03;
        border-left-color: #401b03;
    }
    50% {
        border-right-color: #E9C608;
        border-left-color: #E9C608;
    }
}

@-webkit-keyframes light-mouth {
    0%, 100% {
        border-bottom-color: #401b03;
    }
    50% {
        border-bottom-color: #E9C608;
    }
}

@keyframes light-mouth {
    0%, 100% {
        border-bottom-color: #401b03;
    }
    50% {
        border-bottom-color: #E9C608;
    }
}
.santa {
    position: relative;
    width: 105px;
    height: 165px;
}

.hat {
    position: absolute;
    background: #ef5c53;
    width: 100%;
    height: 45px;
    top: 15px;
    border-radius: 50px 50px 0 0;
    box-shadow: inset 15px 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1;
}

.hat:after {
    content: '';
    position: absolute;
    width: 120%;
    height: 24px;
    background: #fff;
    border-radius: 25px;
    top: 45px;
    margin-left: -10%;
    box-shadow: inset 15px 0 10px rgba(0, 0, 0, 0.1);
}

.hat:before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;;
    background: #fff;
    border-radius: 50%;
    top: -15px;
    left: 0;
    right: 0;
    margin: 0 auto;
    box-shadow: inset 15px 0 15px rgba(0, 0, 0, 0.1);
}


.ear {
    position: absolute;
    width: 24px;
    height: 24px;
    background: #ffe1c7;
    top: 87px;
    border-radius: 50%;
    z-index: -2;
}

.ear.left {
    left: -12px;
}

.ear.right {
    right: -12px;
}

.face {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 85px;
    border-radius: 0 0 60px 60px;
    overflow: hidden;
    z-index: 2;
}

.face i:nth-of-type(1),
.face i:nth-of-type(2) {
    position: absolute;
    width: 45%;
    height: 45%;
    background: #ffe1c7;
    top: 0;
    display: block;
    border-radius: 0 0 45% 45%;
    overflow: hidden;
}

.face i:nth-of-type(1) {
    left: 5%;
}

.face i:nth-of-type(2) {
    right: 5%;
}

.eye {
    position: absolute;
    width: 9px;
    height: 9px;
    background: #000;
    top: 15px;
    border-radius: 50%;
}

.eye.left {
    left: 28px;
}

.eye.right {
    right: 28px;
}

.nose {
    position: absolute;
    width: 15px;
    height: 15px;
    background: #ef5c53;
    top: 24px;
    left: 0;
    right: 0;
    margin: 0 auto;
    border-radius: 50%;
}

.beard {
    position: absolute;
    width: 100%;
    height: 90px;
    background: #f4f4f4;
    bottom: 0;
    border-radius: 0 0 100px 100px;
    box-shadow: inset 0 -10px 10px rgba(0, 0, 0, 0.1);
    z-index: 1;
}

