.pullToRefreshContextContainer {
    color: #5690ff;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.pullToRefreshContextIconContainer {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border-radius: 50%;
    -webkit-animation: rotation 0.75s linear infinite;
    animation: rotation 0.75s linear infinite;
}

@-webkit-keyframes rotation /* Safari and Chrome */
{
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

@keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
.scheduleSwiperContainer {
    display: flex;
    flex-direction: row;
    height: 100px;
    overflow: hidden;
    position: relative;
}

.scheduleSwiper_Page {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
}

.scheduleSwiper_Page.animating {
    -webkit-transition: -webkit-transform 200ms linear;
    transition: -webkit-transform 200ms linear;
    transition: transform 200ms linear;
    transition: transform 200ms linear, -webkit-transform 200ms linear;
}

.scheduleSwiper_Page.left {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}

.scheduleSwiper_Page.center {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.scheduleSwiper_Page.right {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
